* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #a2d6fe;
  color: #222;
}

.booklist {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
}

.heading{
  text-align: center;
  margin-top:50px;
}

@media screen and (min-width: 768px) {/* if 768 and above do this*/
  .booklist {
    grid-template-columns: repeat(3,1fr);/*have 3 repeated cols*/
  }
}

.book {
  background: #fdfcfc;
  border-radius: 1rem;
  padding: 4rem;
  text-align: center;
  position: relative;
  
}
.book img {
  width: 90%;
  object-fit: cover;
  border-radius: 2px;
}

.book h2 {
  margin-top: 1rem;
  font-size: 1rem;
}
.book span{
  position: absolute;
  top: 0;
  left: 0;
  font-size: 1rem;
  padding: 0.75rem;
  border-top-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  background: #c35600;
  color: #fff;
}